:root{
  --dark-bg: #1E1E2F;
  --light-dark-bg: #27293D;
  --theme-text-color: #D27769;
}
*{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.bg-darkish{
  background-color: var(--dark-bg);
}
.bg-light-darkish{
  background-color: var(--light-dark-bg);
}
.login{
  height: 100%;
}

.bg-object{
  background-repeat: no-repeat;
  background-size: cover;
  opacity: .02;
  position: absolute;
}
.bg-object1{
  background-image: url("https://demos.creative-tim.com/blk-design-system-pro-react/static/media/blob.714796fa.png");
  width: 40%;
  height: 75%;
  right: 0px;
  top: 0px;
}

.bg-object2{
  background-image: url("https://demos.creative-tim.com/blk-design-system-pro-react/static/media/path2.2f0ee6e4.png");
  width: 20%;
  height: 50%;
  right: 0px;
  top: 0px;
}

.bg-object3{
  background-image: url("data:image/png;base64,");
  width: 20%;
  height: 40%;
  left: 5%;
  bottom: 5%;
}

.text-dark-orange{
  color: var(--theme-text-color);
}

.login-form-input{
  width: 100%;
  padding: 2%;
  margin: 2%;
  border-radius: 4px;
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.5);
  background-color: transparent;
}

::placeholder{
  color: lightgray
}

.btn-outline-dark-orange {
  color: var(--theme-text-color) !important;
  border-color: var(--theme-text-color) !important;
}

.btn-outline-dark-orange:hover {
  color: #fff !important;
  background-color: var(--theme-text-color) !important;
  border-color: var(--theme-text-color) !important;
}

.btn-dark-orange{
  color: white !important;
  background-color: var(--theme-text-color) !important;
}

.btn-dark-orange:hover{
  color: var(--theme-text-color) !important;
  background-color: transparent !important;
  border-color: var(--theme-text-color);
}

.add-new-btn{
  position: fixed;
  right: 0;
  z-index: 999;
  bottom: 0;
  margin: 2%;
}

.modal{
}
